import React from 'react';
import BurgerMenu from '../BurgerMenu'
import './index.css';
import Navbar from '../Navbar';
import FirstSection from '../FirstSection';
import SecondSection from '../SecondSection';
import VideoSection from '../VideoSection';
import FooterComponent from '../Footer/index'
const mobile = require('../../assets/images/wallpapers/home_mobile.jpg')
const desktop = require('../../assets/images/wallpapers/home_desktop.jpg')

const wallpaper_two = require('../../assets/images/wallpapers/food2.jpg')

const video = require("../../assets/video/video.mp4")
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { width: 0, height: 0 };
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }
  
  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }
  
  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }
  
  updateWindowDimensions() {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  }

  //362 a 
  conditionalImage=() =>{
    if(this.state.width > 700){
      return <FirstSection image={desktop}/>  
    }
    else{
      return <FirstSection image={mobile}/>  

    }
  }
  render(){
  return (
    <div className="App">
      <title>TU RESTAURANTE</title>
     <div className="mainBanner">  CANCÚN, Q.ROO | TODOS LOS DÍAS 12:00 PM A 03:00 AM</div>
     <BurgerMenu/>
     <Navbar/>     
     {this.conditionalImage()}
    <SecondSection image={wallpaper_two}/>
    <VideoSection video={video} text="¡PRUÉBANOS!"/>
    <FooterComponent/>
    </div>
  
    
  );}
}

export default App;
