import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import Home from "./components/Home";
import About from "./components/About";
import Contact from "./components/Contact";
import Menu from "./components/Menu";
import WhatsAppIcon from "@material-ui/icons/WhatsApp";
import { Container, Link, lightColors } from "react-floating-action-button";
import MessengerCustomerChat from "react-messenger-customer-chat";

const whatsappUrl = require("whatsapp-url");

function MainRouter() {
  return (
    <Router>
      <div>
        <Switch>
          <Route path="/home">
            <Home />
          </Route>
          <Route path="/menu">
            <Menu />
          </Route>
          <Route path="/nosotros">
            <About />
          </Route>
          <Route path="/contacto">
            <Contact />
          </Route>
          <Route path="/*">
            <Home />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

class App extends React.Component {
  render() {
    const whatsapp = whatsappUrl({
      phone: "+5219983228470",
      text: "Hola! Quiero más información, por favor.",
      isWeb: true,
    });
    return (
      <div>
        <MainRouter />
        <Container className="float">
          <Link
            href={whatsapp}
            icon="far fa-sticky-note"
            styles={{
              backgroundColor: "#25D366",
              color: lightColors.white,
            }}
          >
            <WhatsAppIcon />
          </Link>
        </Container>

        <MessengerCustomerChat
          className="fbButton"
          pageId="2292059377693483"
          appId="651440219052183"
        />
      </div>
    );
  }
}

export default App;
