import React from 'react';
import BurgerMenu from '../BurgerMenu'
import './index.css';
import Navbar from '../Navbar';
import ContactForm from '../ContactForm';
import Banner from '../Banner'
import FirstSection from '../FirstSection';
const image = require('../../assets/images/wallpapers/contact.jpg')
const image_desktop = require('../../assets/images/wallpapers/contact_desktop.jpg')
class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.state = { width: 0, height: 0 };
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }
  
  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }
  
  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }
  
  updateWindowDimensions() {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  }

  
  conditionalImage=() =>{
    if(this.state.width > 700){
      return <ContactForm image={image_desktop}/>  
    }
    else{
      return <ContactForm image={image}/>  

    }
  }
  render () {
console.log((this.state.width));
 return (
    <div className="App">
      <title>TU RESTAURANTE</title>
      <Banner/>
    <BurgerMenu/>
     <Navbar/>    
      {this.conditionalImage()}
  
    </div>
  
    
  );
}
}



export default Contact;
